<template>
    <body>
        <div class="container">
            <div class="column2">
                <div class="column2-1">
                    <div id="wenzi">
                        <letter></letter>
                    </div>
                </div>
                <div class="column2-2">
                    <el-form class="login-container" label-position="left"  label-width="0px">
                        <h3 class="login-title">
                            系统登录
                            <el-button type="primary"  @click="toRegister()">点击注册</el-button>
                        </h3>
                        <el-form-item label="">
                            <el-input type='text' v-model="loginForm.name" auto-complete="off" placeholder="账号"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input  type='password' v-model="loginForm.password" auto-complete="off" placeholder="密码"></el-input>
                        </el-form-item>
                        <el-form-item >
                            <el-button type="primary" style="width:100%;background: #409EFF;" v-on:click="Login()">登录</el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="clear"></div>
            </div>
        </div>
        <ul class="bg-squares">
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
        </ul>
    </body>
</template>

<script>
    import letter from "@/components/Main/letter";
    import "@/CSS/back.css";
    export  default {
        name:'Login',
        components: {letter},
        data() {
            return {
                loginForm: {
                    name: '',
                    password: '',
                }
            }
        },
        methods:{
            Login(){
                this.axios.post('http://localhost:8888/user/login',this.loginForm).then((resp) =>{
                  let data=resp.data
                  if(data.data=="success"){
                    this.loginForm={};
                    this.$message({
                      message: '登陆成功',
                      type: 'success'
                    });
                    this.$router.push({path:'/Home'})
                  }
                  else {
                    this.$message({
                      message: '账号或密码错误',
                      type: 'fail'
                    });
                  }
                })
            },
            toRegister(){
                this.$router.push({path:'/Register'})
            },
        },

    }
</script>

<style>

    #wenzi{
        width:50px;
        height:50px;
    }
    /*body{*/
    /*    margin: 0px;*/
    /*    padding: 0px;*/
    /*}*/
    .login-container{
        border-radius: 15px;
        background-clip: padding-box;
        margin: 90px auto;
        width: 300px;
        padding: 35px 35px 15px 35px;
        background: Transparent;
        border: 1px solid #eaeaea;
    }
    .login-title{
        margin: 0px auto auto auto;
        text-align: center;
        color: #606266;
    }
    .container {
        height: 100%;
        width: 100%;
        margin: 0px auto auto auto;
    }
    .column2{
        width:100%;
        height:100%;
    }
    .column2-1{
        width:70%;
        height:30%;
        float:left;

    }
    .column2-2{
        width:30%;
        height:30%;
        float:right;
    }
    /*.clear{*/
    /*    clear:both;*/
    /*}*/

</style>
