import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Register from '../views/Register.vue'
import Home from '../views/Home.vue'
import Route_planning from '../views/Route_planning.vue'
import List from '../views/List.vue'
import Demand from "@/components/Select_form/Demand";
import Line_select from "@/components/Select_form/Line_select";
import MY from '../views/My.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/MY',
    name: 'MY',
    component:MY
  },
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/Register',
    name: 'Register',
    component: Register
  },
  {
    path: '/Home',
    name: 'Home',
    component: Home
  },
  {
    path: '/Route_planning',
    name: 'Route_planning',
    component: Route_planning,
    children:[
        {
      path:'/',
      name:'Demand',
      component:Demand
      },
      {
        path:'/Route_planning/Line_select',
        name:'Line_select',
        component:Line_select
      }

    ]


  },
  {
    path: '/List',
    name: 'List',
    component: List
  },
  // 在实际开发中，为了避免加载时间过长，通常使用路由懒加载。
  // compent:() ...
]

const router = new VueRouter({
  //mode:hash  路由的模式，默认位hash,还有history history优点：看起来像真是的路径；缺点：如果访问一个不存在在的路由，他会向后台发起请求。
  // base:process.env.BASE_URL   根地址
  routes
})
// es6  默认导出
export default router
