<template>
    <body>
    <div class="demand_form">
      <el-form :model="Demand_form" ref="Demand_form" :rules="rules"  class="demand_container">
        <el-form-item label="所属省市:" prop="province">
          <el-select v-model="Demand_form.province" placeholder="请选择省份（直辖市）" @change="renderCity" clearable>
            <el-option v-for="item in provinceListAll" :key="item.name" :value="item.name"></el-option>
          </el-select>
          <el-select v-model="Demand_form.city"  placeholder="请选择城市" v-if="citySelectAble" clearable>
            <el-option v-for="item in cityListAll" :key="item.name" :value="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="定制景区">
          <el-checkbox-group v-model="Demand_form.checkListOne">
              <el-checkbox label="风景名胜" name="type"style="color: #eaeaea"></el-checkbox>
              <el-checkbox label="餐饮服务" name="type"style="color: #eaeaea"></el-checkbox>
              <el-checkbox label="生活服务" name="type"style="color: #eaeaea"></el-checkbox>
              <el-checkbox label="公共设施" name="type"style="color: #eaeaea"></el-checkbox>
              <el-checkbox label="购物服务" name="type"style="color: #eaeaea"></el-checkbox>
              <el-checkbox label="商务住宅" name="type"style="color: #eaeaea"></el-checkbox>
              <el-checkbox label="交通设施服务" name="type"style="color: #eaeaea"></el-checkbox>
              <el-checkbox label="地名地址信息" name="type"style="color: #eaeaea"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="酒店要求">
          <el-checkbox-group v-model="Demand_form.checkListTwo">
            <el-checkbox label="离市区近" name="type" style="color: #eaeaea"></el-checkbox>
            <el-checkbox label="含购物中心" name="type"style="color: #eaeaea"></el-checkbox>
            <el-checkbox label="离景区近" name="type"style="color: #eaeaea"></el-checkbox>
            <el-checkbox label="离车站近" name="type"style="color: #eaeaea"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
          <el-button  @click="resetForm('ruleForm')" >取消</el-button>
        </el-form-item>
      </el-form>
    </div>
    </body>
</template>

<script>
import eventBus from '@/eventBus/eventBus.js'


export default {
  data(){
    return{
      Demand_form:{
        id: null,
        province: '',  // 所属省（直辖市）
        city: '',     // 所属市
        delivery: '',
        checkListOne: [],
        checkListTwo: [],
      },
      modal1: false,
      isEdit: false,

      rules:{    // 用于表单验证的规则
        province:[{ required: true, message: '请输入所属省份（或者直辖市）', trigger: 'blur' }],
      },
      provinceListAll: [],      // 全国34个省份（包括直辖市）列表
      cityListAll: [],        // 全国各个城市列表
      citySelectAble: false,    // 城市是否可选
    }
  },
  watch: {
    // watch 监听 Demand_form.province，当城市选择后，再次换省份时，能清空已选择的城市
    'Demand_form.province': function (newValue, oldValue) {
      this.Demand_form.city = '';
    }
  },
  created() {
    // 页面加载时调用的函数
    this.getAllProvinceAndCityList();
  },
  methods: {
    getAllProvinceAndCityList() {     // 获取全国所有省份和城市的列表
      const that = this;
      this.$http.get('https://restapi.amap.com/v3/config/district',{   // 高德地图的API
        params:{
          key:'312f6b1e67b5ecfac5de9acfe970248d', // 这个是申请的 Key，读者自行写入
          keywords: '中国',
          subdistrict: 3,
          extensions: 'base'
        }
      }).then((response) => {
        that.provinceListAll = response.data.districts[0].districts;
        for (let i = 0; i < response.data.districts[0].districts.length; i++) {
          for (let j = 0; j < response.data.districts[0].districts[i].districts.length; j++) {
            that.cityListAll.push(response.data.districts[0].districts[i].districts[j]);
          }
        }
      });
    },
    renderCity() {    // 根据选择的省份，展示该省份相对应的城市列表，未选择省份时，不能选择城市
      if(this.Demand_form.province.length > 0) {
        this.cityListAll = this.provinceListAll.filter(item => item.name == this.Demand_form.province)[0].districts;
        this.citySelectAble = true;
      } else if(!this.Demand_form.province) {
        this.citySelectAble = false;
        this.Demand_form.city = '';     // 清空省份选择时，清空之前选择的城市能进行重新选择城市
      }
    },
    submitForm(Demand_form) {

      this.$router.push({path:'/Route_planning/Line_select'})
    },
    resetForm(Demand_form) {
      this.$refs.Demand_form.resetFields();
    },
  },
  destroyed() {
    eventBus.$emit("sharePlace",this.Demand_form);
  }
}
</script>


<style>
.demand_form{
    left:350px;
    top:150px;
    background-position:center;
    height: 100%;
    width: 50%;
    background-size: cover;
    position: fixed;

}
bady{
  margin: 0px;
  padding: 0px;
}
.demand_container{

}


</style>