<template>
  <div class="map-wrap">
      <Base_map ref='map'class="map" ></Base_map>
  </div>
</template>

<script>
import Base_map from "@/components/Map/Base_map";
import eventBus from "@/eventBus/eventBus";
export default {
  name: 'Line_select',
  components: {Base_map},
  data() {
    return {
      address:[],
      addressNew:"",
      city:""
    }
  },
  methods:{
    setMapCenter(data){
      this.$refs.map.setMapCenter(data)
    },
  },
  mounted() {
    this.setMapCenter(this.addressNew)
  },
  created() {
    eventBus.$on('sharePlace',(data)=>{
      this.address[0] = data.province
      this.address[1] = data.city
      this.addressNew=this.address.join("")
    })
  },
}
</script>

<style lang="less" scoped>
  #map-wrap{
    height: 100%;
    width: 100%;
}
</style>



