<template>
    <div class='container'>
        <div id="main" style="width:600px;height:300px;"></div>
    </div>
</template>

<script>
    import * as echarts from 'echarts/core';
    import { GraphicComponent } from 'echarts/components';
    import { CanvasRenderer } from 'echarts/renderers';
    export default {
        name: "letter",
        mounted() {
            this.initCharts();  //初始化渲染echart图标
        },
        methods: {
            initCharts() {
                //  获取数据
                echarts.use([GraphicComponent, CanvasRenderer]);
                var chartDom = document.getElementById('main');
                var myChart = echarts.init(chartDom);
                var option;
                option = {
                    graphic: {
                        elements: [
                            {
                                type: 'text',
                                left: 'center',
                                top: 'center',
                                style: {
                                    text: 'Sign In to\n' +
                                        'Open the World',
                                    fontSize: 70,
                                    fontWeight: 'bold',
                                    lineDash: [0, 200],
                                    lineDashOffset: 0,
                                    fill: 'transparent',
                                    stroke: 'white',
                                    lineWidth: 1
                                },
                                keyframeAnimation: {
                                    duration: 3000,
                                    loop: true,
                                    keyframes: [
                                        {
                                            percent: 0.7,
                                            style: {
                                                fill: 'transparent',
                                                lineDashOffset: 200,
                                                lineDash: [200, 0]
                                            }
                                        },
                                        {
                                            // Stop for a while.
                                            percent: 0.8,
                                            style: {
                                                fill: 'transparent'
                                            }
                                        },
                                        {
                                            percent: 1,
                                            style: {
                                                fill: 'white'
                                            }
                                        }
                                    ]
                                }
                            }
                        ]
                    }
                };
                option && myChart.setOption(option);
            }
        }
    }
</script>
<style lang="less" scoped>
  .container{
    height:100% ;
    width:100%;
    position: relative;
  }
</style>