<template>
  <div id="outer-box">
    <div id="mapContainer"></div>
      <input id="reGeo" type="button" class="btn" value="生成路径" v-on:click="PostInfo()"></input>
      <input id="clear" type="button" class="btn" value="清除标点" v-on:click="Clear()" ></input>
    <div id="panel"></div>
    <div id="panel1">
      <div id="intro">
        <h3>周边搜素</h3>
      </div>
      <ul id="my-list"></ul>
    </div>
    <div id="pickerBox">
      <input id="pickerInput" placeholder="输入关键字选取地点" />
      <div id="poiInfo"></div>
    </div>
  </div>
</template>

<script>
//高德的安全密钥
import eventBus from "@/eventBus/eventBus";

window._AMapSecurityConfig = {
  securityJsCode: '9e8a5b71496465c31edad6372165a1a6'}
import {AMapManager,lazyAMapApiLoaderInstance} from'vue-amap'
export default {
  name:'',
  data() {
    return {
      map:null,
      zoom:15,
      events:[],
      city:"",
      placeList:[],
      list:[],
      markers :[],
      addressName:[],
      dataAll:[],
      markerList:null
    };
  },
  mounted() {
    lazyAMapApiLoaderInstance.load().then(()=>{
      this.map=new AMap.Map('mapContainer',{
        zoom:this.zoom,
      })
      let map=this.map
      AMapUI.loadUI(['misc/PoiPicker'], (PoiPicker)=> {
        const poiPicker = new PoiPicker({
          input: 'pickerInput'
        });
        //初始化poiPicker
        poiPickerReady(poiPicker);
      });
      function poiPickerReady(poiPicker) {

        window.poiPicker = poiPicker;

        let marker = new AMap.Marker({
          map: map
        });

        //选取了某个POI
        poiPicker.on('poiPicked', (poiResult)=> {

          const source = poiResult.source,
              poi = poiResult.item,
              info = {
                source: source,
                id: poi.id,
                name: poi.name,
                location: poi.location.toString(),
                address: poi.address
              };
          marker.setPosition(poi.location);
          map.setCenter(marker.getPosition());
        });

        poiPicker.onCityReady(function() {
          poiPicker.suggest('美食');
        });
      }


      this.map.on('click',(e)=>{
        //获取每个标记点的坐标，并将其返回成列表形式
        let lat=e.lnglat.lat;
        let lng=e.lnglat.lng;
        let lnglat=new AMap.LngLat(lng,lat);
        let PlaceList=[];
        PlaceList.push(""+lng,""+lat)
        this.list.push(PlaceList)

        // //获取每个标记点的名称,并将其添加到已选地
        // this.getPlaceName(lnglat,(address)=> {
        //   let addressName = address
        //   let newItem = '<div class="input-item">'+addressName+
        //       '</div>';
        //   document.getElementById('postions').insertAdjacentHTML('beforebegin',newItem)
        // })
        //为点击目标添加标记点
        //创建icon
        let icon = new AMap.Icon({
          size: new AMap.Size(50,50),
          image: require('@/img/tp.webp'),
          imageSize: new AMap.Size(50, 50)
        });
        let marker=new AMap.Marker({
          position:new AMap.LngLat(lng,lat),
          map:this.map,
          icon:icon
        })
        this.getPlaceName(lnglat,(address)=>{
          let addressName=address
          marker.setLabel({
            offset: new AMap.Pixel(10, 10),  //设置文本标注偏移量
            content: '<div class="info" style="color: red;border-style: groove">'+addressName+'</div>', //设置文本标注内容
            direction: 'top' ,//设置文本标注方位
            position: 'absolute',
          });
        })
        this.map.add(marker)
        this.markers.push(marker);
        //为每个点标记查找周围选定类型的服务
        this.axios.post('http://localhost:8082/'+this.city+'/SearchNearby?x='+lng+'&'+'y='+lat).then((resp) =>{
          //生成一个信息窗体
          // let infoWindow = new AMap.InfoWindow({
          //   isCustom:	false,
          //   draggable: true,  //是否可拖动
          //   offset: new AMap.Pixel(0, -31),
          //   open:true,
          //   map:this.map,
          // });

          //为每个返回的服务位置添加标记
          // for (let i = 0;i<resp.data.length;i++){
          //   let marker=new AMap.Marker({
          //     position:resp.data[i],
          //     map:this.map,
          //     title:'place'
          //   });
          //   let lnglat=new AMap.LngLat(resp.data[i][0],resp.data[i][1]);
          //   this.map.add(marker)
          //   this.markers.push(marker);
          //   //给每个地点添加地名
          //   this.getPlaceName(lnglat,(address)=>{
          //     let addressName = address
          //     marker.on('click',()=>{
          //       marker.setLabel({
          //         offset: new AMap.Pixel(10, 10),  //设置文本标注偏移量
          //         content: '<div class="info">' + addressName + '</div>', //设置文本标注内容
          //         direction: 'right',//设置文本标注方位
          //         position: 'absolute',
          //       });
          //     })
          //   })
          //   this.map.add(marker)
          //   this.markers.push(marker);
          //   // 为每一个marker添加点击信息窗体
          //   // marker.content = "地理位置：";
          //   // marker.on('click', markerClick);
          //   // marker.emit('click', {target: marker});
          // }
          // function markerClick(e) {
          //   infoWindow.setPosition(e.target.getPosition())
          //   infoWindow.setContent(e.target.content);
          // }
          // this.map.setFitView();
          AMapUI.loadUI(['misc/MarkerList', 'overlay/SimpleMarker', 'overlay/SimpleInfoWindow'],(MarkerList, SimpleMarker, SimpleInfoWindow)=>{
            let markerList = new MarkerList({
              //关联的map对象
              map: this.map,

              //列表的dom容器的id
              listContainer: 'my-list',

              //需要监听的列表节点事件
              listElementEvents: ['click', 'mouseenter', 'mouseleave'],

              //需要监听的marker事件
              markerEvents: ['click', 'mouseover', 'mouseout'],

              //需要监听的infoWindow事件
              infoWindowEvents: ['click', 'mouseover', 'mouseout'],

              //返回数据项的Id
              // getDataId: function (dataItem, index) {
              //   //index表示该数据项在数组中的索引位置，从0开始，如果确实没有id，可以返回index代替
              //   return dataItem.id;
              // },
              //返回数据项的位置信息，需要是AMap.LngLat实例，或者是经纬度数组，比如[116.789806, 39.904989]
              getPosition:  (dataItem)=> {
                return dataItem.position;
              },
              //返回数据项对应的Marker
              getMarker:  (dataItem, context, recycledMarker)=> {
                // const label = dataItem.id;
                //存在可回收利用的marker
                if (recycledMarker) {
                  //直接更新内容返回
                  // recycledMarker.setIconLabel(label);
                  return recycledMarker;
                }

                //返回一个新的Marker
                return new SimpleMarker({
                  containerClassNames: 'my-marker',
                  // iconLabel: label
                });
              },
              //返回数据项对应的列表节点
              getListElement:  (dataItem, context, recycledListElement)=> {
                const tpl = '<p><%- dataItem.desc %>';
                const content = MarkerList.utils.template(tpl, {
                  dataItem: dataItem,
                  dataIndex: context.index
                });

                if (recycledListElement) {
                  //存在可回收利用的listElement, 直接更新内容返回
                  recycledListElement.innerHTML = content;
                  return recycledListElement;
                }

                //返回一段html，MarkerList将利用此html构建一个新的dom节点
                return '<li style="list-style-type: none;border: 10px solid #008CFF;" >' + content + '</li>';
              },
              getInfoWindow:  (dataItem, context, recycledInfoWindow)=> {

                if (recycledInfoWindow) {
                  //存在可回收利用的infoWindow, 直接更新内容返回
                  // recycledInfoWindow.setInfoTitle(dataItem.id);
                  recycledInfoWindow.setInfoBody(dataItem.desc);
                  return recycledInfoWindow;
                }

                //返回一个新的InfoWindow
                return new SimpleInfoWindow({
                  offset: new AMap.Pixel(0, -57),
                  infoBody: dataItem.desc
                });
              }
            });
            //构建一个数据项数组，数据项本身没有格式要求，但需要支持下述的getDataId和getPosition
            for (let i = 0;i<resp.data.length;i++) {
              let lnglat=new AMap.LngLat(resp.data[i][0],resp.data[i][1]);
              this.getPlaceName(lnglat,(address)=> {
                let addressName = address
                const data = {
                  position: [lnglat.lng,lnglat.lat],
                  desc:addressName
                }
                this.dataAll.push(data)
                markerList.render(this.dataAll)
              })
            }
            markerList.on('listElementClick listElementMouseenter listElementMouseleave ' +
                'markerClick markerMouseover markerMouseout ' +
                'infoWindowClick infoWindowMouseover infoWindowMouseout',
                (event, record)=> {
                  const $ = MarkerList.utils.$,
                      template = MarkerList.utils.template;

                  $('#eventInfo').html(template(
                      '<div class="eventType"><%- event.type %></div>', {
                    event: event,
                    // record: record
                  }));
                });
            //监听选中改变
            markerList.on('selectedChanged', function(event, info) {
              console.log(event, info);
            });
          });
        })
      })
    })
  },
  created() {
    eventBus.$on('sharePlace',(data)=>{
      this.city = data.city
    })
  },
  methods: {
    PostInfo(){
      //构造路线导航类
      const driving = new AMap.Driving({
        map: this.map,
        panel: "panel"
      });
      this.axios.post('http://localhost:8082/'+this.city+'/mapPlan',this.list).then((resp) =>{
        let max_value=resp.data.length-1;
        let way_path= {
          waypoints:[],
        };
        for (let i=1;i<max_value;i++){
          way_path.waypoints.push(new AMap.LngLat(resp.data[i][0],resp.data[i][1]))
        }
        console.log(way_path)
        driving.search(new AMap.LngLat(resp.data[0][0],resp.data[0][1]), new AMap.LngLat(resp.data[max_value][0],resp.data[max_value][1]),way_path,function(status, result) {
          // result 即是对应的驾车导航信息，相关数据结构文档请参考  https://lbs.amap.com/api/javascript-api/reference/route-search#m_DrivingResult
          if (status === 'complete') {
            console.log('绘制驾车路线完成')
          } else {
            console.log('获取驾车数据失败：' + result)
          }
        });
      })
    },
    addressSetMapCenter(address) {
      const geocoder = new AMap.Geocoder({
      });
      geocoder.getLocation(address,(status, result) => {
        if (status == 'complete' && result.info == 'OK') {
          let lat=result.geocodes[0].location.lat;
          let lng=result.geocodes[0].location.lng;
          this.map.setCenter([lng,lat])
        }
      });
    },
    setMapCenter(value) {
      this.addressSetMapCenter(value)
    },
     // 将这个随意的位置转化为地理信息
    getPlaceName(lnglat,callback){
      let geocoder=new AMap.Geocoder({
        city:'全国'
      })
      geocoder.getAddress(lnglat, (status, result)=> {
        if (status === 'complete'&&result.regeocode) {
          let address = result.regeocode.formattedAddress;
          callback(address)
        }else{
          console.log('根据经纬度查询地址失败')
        }
      });
    },
    Clear() {
      let ul=document.getElementById("my-list");
      while(ul.hasChildNodes()) //当div下还存在子节点时 循环继续
      {
        ul.removeChild(ul.firstChild);
      }
      this.map.clearMap()
      this.map.remove(this.markers)
      this.markers=[]
      this.map.remove(this.dataAll)
      this.dataAll=[]
    }
  }
}
</script>
<style scoped>
#outer-box{
  height:100%;
  width:100%;
}
#mapContainer {
  height:100vh;
  width: 100vw;
}
#reGeo {
  position: absolute;
  z-index: 9999;
  right: 400px;
  top: 560px;
  padding-left:4px;
  width:8rem;
  background-color: gray;
}
#clear{
  position: absolute;
  z-index: 9999;
  right: 240px;
  top: 560px;
  padding-left:4px;
  width:8rem;
  background-color: gray;
}
#panel1{
  width: 220px;
  height: 100%;
  position: absolute;
  z-index: 9999;
  right: 0px;
  top: 0px;
  background-color: white;
  overflow:auto
}
#pickerBox {
  position: absolute;
  z-index: 9999;
  top: 50px;
  right: 230px;
  width: 300px;
}

#pickerInput {
  width: 200px;
  padding: 5px 5px;
}

#poiInfo {
  background: #fff;
}
</style>



