<template>
  <body>
    <div id="home">
      <header>
        <Top_index>
        </Top_index>
      </header>
      <div class="showImg" >
        <!--          //轮播图片-->
        <img  @mouseover="changeInterval(true)"
              @mouseleave="changeInterval(false)"
              v-for="(item) in imgArr"
              :key="item.id"
              :src="item.url"
              alt="暂无图片"
              v-show="item.id===currentIndex"
        >
        <!--          //左侧按钮-->
        <div  @click="clickIcon('up')"   class="iconDiv icon-left">
          <i class="el-icon-caret-left"></i>
        </div>
        <!--          //右侧按钮-->
        <div  @click="clickIcon('down')"  class="iconDiv icon-right">
          <i class="el-icon-caret-right"></i>
        </div>
        <!--          //控制圆点-->
        <div class="banner-circle">
          <ul>
            <li @click="changeImg(item.id)"
                v-for="(item) in imgArr"
                :key="item.id"
                :class="item.id===currentIndex? 'active': '' "
            ></li>
          </ul>
        </div>
      </div>
      <div class="chan">
        山山而川，征途慢慢。
      </div>
      <div class="box1">
        <img src="../img/b.jpg" >
      </div>
      <div class="box2">
        <img src="../img/bb.jpg" >
      </div>
      <div class="box3">
        <img src="../img/bbb.jpg" >
      </div>
      <div class="ming">

      </div>
    </div>
  </body>
</template>

<script >
import Top_index from "@/components/Index/Top_index";
export default {
  name:'Home',
  components: {Top_index},
  data(){
    return {
      currentIndex :0,//当前所在图片下标
      timer:null,//定时轮询
      imgArr:[
        {	id:0,
          url: require('../img/fengjing3-1.jpg'),
        },{
          id:1,
          url: require('../img/fengjing1.jpg'),
        },{
          id:2,
          url: require('../img/fengjing2.jpg'),
        },
      ]
    }
  },
  methods:{
    //开启定时器
    startInterval(){
      // 事件里定时器应该先清除在设置，防止多次点击直接生成多个定时器
      clearInterval(this.timer);
      this.timer = setInterval(()=>{
        this.currentIndex++;
        if(this.currentIndex > this.imgArr.length-1){
          this.currentIndex = 0
        }
      },3000)
    },
    // 点击左右箭头
    clickIcon(val){
      if(val==='down'){
        this.currentIndex++;
        if(this.currentIndex===this.imgArr.length){
          this.currentIndex = 0;
        }
      }else{
        /* 第一种写法
this.currentIndex--;
if(this.currentIndex < 0){
this.currentIndex = this.imgArr.length-1;
} */
        // 第二种写法
        if(this.currentIndex === 0){
          this.currentIndex = this.imgArr.length;
        }
        this.currentIndex--;
      }
    },
    // 点击控制圆点
    changeImg(index){
      this.currentIndex = index
    },
    //鼠标移入移出控制
    changeInterval(val){
      if(val){
        clearInterval(this.timer)
      }else{
        this.startInterval()
      }
    }
  },
  //进入页面后启动定时轮询
  mounted(){
    this.startInterval()
  }
}

</script>
