            type: "map",
            mapType: "china",
            zlevel: 1,
            roam: true,
            geoIndex: 0,
            tooltip: {
              show: true,
            },
            itemStyle: {
              areaColor: "#00196d",
              borderColor: "#0a53e9",
            },
            emphasis: {
