<style>
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
  outline: none;
  box-sizing: border-box;
}
html {
  margin: 0;
  padding: 0;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.2em;
  background-color: #f1f1f1;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
a {
  color: #343440;
  text-decoration: none;
}
#List {
  height: 100vh;
  width: 100vw;
  position: relative;
 }
header {
  background: dimgrey;
  width: auto;
  height: 80px;
}
#container{
  background-position:center;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
  margin: 0px;
  padding: 0px;
  overflow: auto;
}

#index {
  color: #d3d6dd;
  width: 1920px;
  height: 1080px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform-origin: left top;
  overflow: auto;
}
  .bg {
    /*//整体页面背景*/
    width: 100%;
      height: 100%;
      padding: 16px 16px 0 16px;
      /*background-image: url("../img/backg.jpg");*/
    /*//背景尺寸*/
    background-size: cover;
    /*//背景位置*/
    background-position: center center;
    }
    /*//顶部右边装饰效果*/
  .title_left {
    width: 100%;
    height: 50px;
  }
    /*//顶部左边装饰效果*/
  .title_right {
